import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Employee } from './model/Employee';

@Injectable({
  providedIn: 'root'
})
export class EmployeeService {
  apiUrl= 'https://5db9339b177b350014ac8138.mockapi.io/Employee';
  updateapiUrl= 'https://5db9339b177b350014ac8138.mockapi.io/Employee/';
  constructor(private _httpClient:HttpClient){
  }
  
  getData() 
  {
      const urlApi = this.apiUrl;
      return this._httpClient.get<any>((`${urlApi}`));
  }

  deleteData(id: number): Observable<any>
  {
    const urlApi= this.apiUrl;
    return this._httpClient.delete<any>((`${urlApi}/${id}`));
  }

  addData(Name,Email,Phone,Address,Avatar): Observable<any>{
    const urlApi=this.updateapiUrl;
    let data={
      'Name': Name,
      'Email': Email,
      'Phone': Phone,
      'Address': Address,
      'Avatar':Avatar
    }
    return this._httpClient.post<Employee>(`${urlApi}`,data);
  }

  updateData(id:any,body:any){
    return this._httpClient.put<Employee>(`${this.updateapiUrl}${id}`,body);

  }

  detailData(id:number){
    return this._httpClient.get<Employee>(`${this.updateapiUrl}${id}`);
  }


  isEmail(email): any {
    const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
  }

  // uploadImage(url,data):Observable<any>{
  //   url='https://5db9339b177b350014ac8138.mockapi.io/Employee';
  //   return this.httpClient.post(`${url}`,data);
  // }
}
